import { Component, OnInit, ViewEncapsulation, ViewChild, TemplateRef } from '@angular/core';
import { Contribution } from './contribution';
import { ContributionsService } from './contributions.service';

import { Router, RouterModule } from '@angular/router';
import { ModalPreviewComponent } from '../heart-map/modal-preview.component';


import { overlayConfigFactory } from "angular2-modal";
import {
  VEXBuiltInThemes,
  Modal,
  VEXModalContext
} from 'angular2-modal/plugins/vex';


@Component({
  selector: 'app-contributions',
  templateUrl: './contributions.component.html',
  styleUrls: ['./contributions.component.scss']
})
export class ContributionsComponent implements OnInit {

  constructor(
  	private service: ContributionsService,
  	private router: Router,
    public modal: Modal
  	) { }

  contributions: Contribution[];

  contextContribution: Contribution;

  theme: VEXBuiltInThemes = <VEXBuiltInThemes>'default';

  @ViewChild('templateRef') public templateRef: TemplateRef<any>;

  ngOnInit() {

    this.service.getContributions()
      .then( contributions => 
        this.contributions = contributions
    );

  	
  }


  edit(contribution) {
  	this.router.navigate(['/contributions/edit', contribution.id]);

  }

  previewContribution(id:string) {

    this.service.getContribution(id)
      .then(contribution=>this.contextContribution=contribution)
      //.then(story=>this.contextStory=story);

    this.modal
          .open(this.templateRef, overlayConfigFactory({ isBlocking: false }, VEXModalContext))

  }



}



