import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HeartMapComponent } from '../heart-map/heart-map.component';
import { StyledemoComponent } from '../styledemo/styledemo.component';


const routes: Routes = [
	{ path: 'heartmap',  component: HeartMapComponent },
	{ path: 'styledemo',  component: StyledemoComponent },
	{ path: '',   redirectTo: '/heartmap', pathMatch: 'full' },
	{ path: '**', redirectTo: '/heartmap', pathMatch: 'full' },
];

@NgModule({
  imports: [RouterModule.forRoot(
  		routes,
		{ enableTracing: true } // <-- debugging purposes only
  		)],
  exports: [RouterModule]
})
export class AppRoutingModule { }



