import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import { AppRoutingModule } from './route/app-routing.module';

import { HeaderComponent } from './header/header.component';
import { MainComponent } from './main/main.component';

import { HeartMapComponent } from './heart-map/heart-map.component';
import { ModalPreviewComponent } from './heart-map/modal-preview.component';


import { AgmCoreModule } from '@agm/core';
import { ContributionsModule } from './contributions/contributions.module';
import { StyledemoComponent } from './styledemo/styledemo.component';

import { ContributionsService } from './contributions/contributions.service';

import { VexModalModule } from 'angular2-modal/plugins/vex';

import { ModalModule, OverlayRenderer, DOMOverlayRenderer, Overlay } from 'angular2-modal';
import { FooterComponent } from './footer/footer.component';
import { AnimateBounceDirective } from './shared/animate-bounce.directive';



const MODAL_PROVIDERS = [
  Overlay,
  { provide: OverlayRenderer, useClass: DOMOverlayRenderer }
];


@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent, 
    MainComponent,
    HeartMapComponent,
    StyledemoComponent,
    ModalPreviewComponent,
    FooterComponent,
    AnimateBounceDirective
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    ContributionsModule,
    AppRoutingModule,
    AgmCoreModule.forRoot({
      apiKey: 'AIzaSyCm1ee9Y1iSjJGzJ-5yTYTESFWJaAk8aKo',
      libraries: ["places"]
    }),
    VexModalModule
  ],
  
  bootstrap: [AppComponent],
  providers: [ 
    ContributionsService,
    MODAL_PROVIDERS
     ]
})
export class AppModule { }



