import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ContributionFormComponent } from './contribution-form.component';
import { ContributionsComponent } from './contributions.component';


const routes: Routes = [
	{ path: 'contributions/new',  component: ContributionFormComponent },
	{ path: 'contributions/edit/:id',  component: ContributionFormComponent },
	{ path: 'contributions',  component: ContributionsComponent }

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ContributionsRoutingModule { }



