import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { ReactiveFormsModule } from '@angular/forms';

import { ContributionsRoutingModule } from './contributions-routing.module';


import { ContributionFormComponent, MapContentComponent } from './contribution-form.component';
import { ContributionsComponent } from './contributions.component';

import { ContributionsService } from './contributions.service';

import { AgmCoreModule, GoogleMapsAPIWrapper } from '@agm/core';

import { LoadingIndicatorComponent } from '../loading-indicator/loading-indicator.component';


@NgModule({
  imports: [
    CommonModule,
    ReactiveFormsModule,
    ContributionsRoutingModule,
    AgmCoreModule.forRoot({
      apiKey: 'AIzaSyCm1ee9Y1iSjJGzJ-5yTYTESFWJaAk8aKo',
      libraries: ["places"]
    })

  ],
  declarations: [
    ContributionsComponent,
    ContributionFormComponent,
    MapContentComponent,
    LoadingIndicatorComponent
    
  ],
  providers: [
  	ContributionsService,
    GoogleMapsAPIWrapper
    
  ]
})
export class ContributionsModule { }



